}

.box {
    height: 220px;
    width: 320px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;

    overflow: hidden;
    box-shadow: 1px 1px 5px 1px #0505A9;
    border-radius: 50px 4px 50px 4px;
    position: relative;
}

.box:hover .imagg {
    opacity: 0.5;
    filter: blur(1px);
}

.box:hover .imagDiv {

    opacity: 1;
}

.imagg {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    height: 100%;
    transition: .5s ease;
    backface-visibility: hidden;
}



.vst {
    display: block;
    margin: auto;
    border: unset;
    padding: 10px 25px;
    border-radius: 5px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 15px;


}

.imagDiv {
    transition: .5s ease;

    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}


@media only screen and (max-width: 768px) {
    .titleportfolio {
        font-size: 40px;

    }




}


@media only screen and (max-width: 375px) {

    .box {
        height: 200px;
        width: 300px;

    }

}